import React from 'react'
import {init} from './slider'
import {BackButton} from '../back-button'
import './slider.css'

export function Slider(props) {
  const ref = React.useRef()
  const titles = props.titles.concat(props.titles[0])

  React.useEffect(() => {
    let destroy
    if (ref.current) {
      console.log('init')
      destroy = init(ref.current)
    }

    return () => {
      console.log('destroy')
      destroy && destroy()
    }
  }, [])

  return (
    <div
      className="slider-wrap"
      style={{backgroundColor: props.backgroundColor}}
    >
      <BackButton to="/works" />
      <div className="slider | js-drag-area">
        <div className="slider__inner | js-slider" ref={ref}>
          {props.imgs.map((img, index) => (
            <div
              className="slide | js-slide"
              key={img + index}
              style={{left: index ? `${index * 120}%` : 0}}
            >
              <div className="slide__inner | js-slide__inner">
                <img
                  className="js-slide__img"
                  src={img}
                  alt="app screenshot"
                  crossOrigin="anonymous"
                  draggable="false"
                />
              </div>
            </div>
          ))}
        </div>
      </div>

      <div className="slide-titles">
        <div className="slide-title slide-title--placeholder">LOREM IPSUM</div>

        <div className="slide-titles__list | js-titles">
          {titles.map((title, index) => (
            <div key={index} className="slide-title | js-title">
              {title}
            </div>
          ))}
        </div>
      </div>

      <div className="progress">
        <div className="progress__line | js-progress-line"></div>
        <div className="progress__line | js-progress-line-2"></div>
      </div>
    </div>
  )
}
