import React from 'react'
import imagesLoaded from 'imagesloaded'

export function useImgPreload(imgSrcs) {
  const [loading, setLoading] = React.useState(true)

  React.useEffect(() => {
    const preloader = srcs => {
      return new Promise(resolve => {
        const imgwrap = document.createElement('div')
        imgwrap.style.visibility = 'hidden'
        document.body.appendChild(imgwrap)

        srcs.forEach(src => {
          const imgEl = document.createElement('img')
          imgEl.style.width = 0
          imgEl.src = src
          imgEl.className = 'preload'
          imgwrap.appendChild(imgEl)
        })

        imagesLoaded(document.querySelectorAll('.preload'), () => {
          imgwrap.parentNode.removeChild(imgwrap)
          resolve()
        })
      })
    }

    preloader(imgSrcs)
      .then(() => {
        setLoading(false)
      })
      .catch(console.error)
  }, [imgSrcs])

  return [loading]
}
