import React from 'react'
import {useHistory} from 'react-router-dom'
import {motion} from 'framer-motion'
import {Link} from 'react-router-dom'
import './project-navigation.css'

function Img(props) {
  const [loading, setLoading] = React.useState(true)

  return (
    <img
      onLoad={() => setLoading(false)}
      style={{opacity: loading ? 0 : 1}}
      alt={props.alt}
      {...props}
    />
  )
}

function Item({index, data, onClick}) {
  const transition = {
    type: 'tween',
    // easing: [0.445, 0.05, 0.55, 0.95],
    easing: [0.215, 0.61, 0.355, 1],
  }
  const rootVariants = {
    hidden: {opacity: 0, x: 200},
    show: {
      opacity: 1,
      x: 0,
      transition: {
        delayChildren: 0.2,
        staggerChildren: 0.2,
        duration: 0.4,
        ...transition,
      },
    },
  }

  const numberVariants = {
    hidden: {x: '3vw'},
    show: {
      x: 0,
      transition,
    },
    hover: {
      y: 10,
      transition,
    },
  }

  const contentVariants = {
    hidden: {opacity: 0, x: 30},
    show: {
      x: 0,
      opacity: 1,
      transition,
    },
    hover: {
      opacity: 0.85,
      y: -10,
      transition,
    },
  }

  return (
    <motion.div
      className="project-item"
      variants={rootVariants}
      whileHover="hover"
      onClick={onClick}
    >
      <div className="project-number">
        <div className="project-number-inner">
          <motion.div variants={numberVariants}>{index}</motion.div>
        </div>
      </div>

      <motion.div variants={contentVariants} className="project-content">
        <h2>{data.title}</h2>
        <h4>{data.sub}</h4>
        <div className="img-ratio__wrap">
          <div className="img-ratio__inner">
            <Img className="img-ratio__img" src={data.src} alt={data.title} />
          </div>
        </div>
        {/* <small>2017.08.18</small> */}
      </motion.div>
    </motion.div>
  )
}

const menus = [
  {
    title: 'TMB',
    sub: 'Absolute Visa',
    to: 'tmb',
    src: '/imgs/tmb/cover.jpg',
  },
  {
    title: 'Krungsri',
    sub: 'Sanook Station',
    to: 'krungsri',
    src: '/imgs/krungsri/cover.jpg',
  },

  {
    title: 'On Demand',
    sub: 'Turn on your future',
    to: 'ondemand',
    src: '/imgs/ondemand/cover.jpg',
  },
  {
    title: 'Estee Lauder',
    sub: 'Advertising Campaign',
    to: 'estee',
    src: '/imgs/estee/cover.jpg',
  },
  {
    title: 'Thawemit',
    sub: 'Moving together',
    to: 'twm',
    src: '/imgs/twm/cover.jpg',
  },
]

export function ProjectNavigation() {
  const history = useHistory()
  const container = {
    hidden: {
      opacity: 0,
      transition: {
        duration: 0.3,
        when: 'afterChildren',
        staggerChildren: 0.1,
        staggerDirection: -1,
      },
    },
    show: {
      opacity: 1,
      transition: {
        duration: 0.3,
        delayChildren: 0.5,
        staggerChildren: 0.1,
      },
    },
  }

  const close = {
    hidden: {opacity: 0, x: -40},
    show: {opacity: 1, x: 0},
  }

  return (
    <motion.div
      className="projects-wrap"
      initial="hidden"
      animate="show"
      exit="hidden"
      variants={container}
    >
      <motion.div className="projects-container">
        {menus.map((data, i) => (
          <Item
            key={data.to}
            index={i + 1}
            data={data}
            onClick={() => history.push(`/works/${data.to}`)}
          />
        ))}
      </motion.div>

      <motion.div variants={close} className="projects-close">
        <div className="line"></div>
        <Link to="/">HOME</Link>
      </motion.div>
    </motion.div>
  )
}
