import React from 'react'
import {BrowserRouter as Router} from 'react-router-dom'
import {Routes} from './routes'
import {ScrollToTop} from './components/scroll-top'
import {gsap} from 'gsap'
// import { Cursor } from './components/cursor'
// import { CursorProvider } from './context/cursor-context'
// import { Navigation } from './components/navigation'
import {lerp} from './utils/math'

const getMousePos = e => {
  let posx = 0
  let posy = 0
  if (!e) e = window.event
  if (e.pageX || e.pageY) {
    posx = e.pageX
    posy = e.pageY
  } else if (e.clientX || e.clientY) {
    posx =
      e.clientX + document.body.scrollLeft + document.documentElement.scrollLeft
    posy =
      e.clientY + document.body.scrollTop + document.documentElement.scrollTop
  }

  return {x: posx, y: posy}
}

let mouse = {x: 0, y: 0}
window.addEventListener('mousemove', e => (mouse = getMousePos(e)))

class Cursor {
  constructor(el) {
    this.DOM = {el: el}
    this.DOM.el.style.opacity = 0

    this.bounds = this.DOM.el.getBoundingClientRect()

    this.renderedStyles = {
      tx: {previous: 0, current: 0, amt: 0.2},
      ty: {previous: 0, current: 0, amt: 0.2},
    }

    this.onMouseMoveEv = () => {
      this.renderedStyles.tx.previous = this.renderedStyles.tx.current =
        mouse.x - this.bounds.width / 2
      this.renderedStyles.ty.previous = this.renderedStyles.ty.previous =
        mouse.y - this.bounds.height / 2
      gsap.to(this.DOM.el, {duration: 0.9, ease: 'Power3.easeOut', opacity: 1})
      requestAnimationFrame(() => this.render())
      window.removeEventListener('mousemove', this.onMouseMoveEv)
    }
    window.addEventListener('mousemove', this.onMouseMoveEv)
  }
  render() {
    this.renderedStyles['tx'].current = mouse.x - this.bounds.width / 2
    this.renderedStyles['ty'].current = mouse.y - this.bounds.height / 2

    for (const key in this.renderedStyles) {
      this.renderedStyles[key].previous = lerp(
        this.renderedStyles[key].previous,
        this.renderedStyles[key].current,
        this.renderedStyles[key].amt,
      )
    }

    this.DOM.el.style.transform = `translateX(${this.renderedStyles['tx'].previous}px) translateY(${this.renderedStyles['ty'].previous}px)`

    requestAnimationFrame(() => this.render())
  }
}

export default function App() {
  const ref = React.useCallback(node => {
    if (node !== undefined) {
      new Cursor(node)
    }
  }, [])
  return (
    <Router>
      <ScrollToTop />
      <Routes />

      <svg
        className="cursor"
        width="80"
        height="80"
        viewBox="0 0 80 80"
        ref={ref}
      >
        <circle className="cursor__inner" cx="40" cy="40" r="20" />
      </svg>
    </Router>
  )
}

