import React, {useState, useEffect, useRef} from 'react'
import clsx from 'clsx'
import {gsap} from 'gsap'
import './home.css'
import {Navigation} from '../navigation'

const data = [
  {
    text: 'helllo',
    src: '/imgs/home/1.jpg',
    color: '#DADEE7',
  },
  {
    text: 'bonjour',
    src: '/imgs/home/2.jpg',
    color: '#FDCDC1',
  },
  {
    text: 'ciaaoo',
    src: '/imgs/home/3.jpg',
    color: '#21A0B8',
  },
  {
    text: 'hollaa',
    src: '/imgs/home/4.jpg',
    color: '#CE5657',
  },
  {
    text: 'alooha',
    src: '/imgs/home/5.jpg',
    color: '#022150',
  },
]
// const colors = ['#0038a6', 'orange', 'pink', 'purple', 'gold']

export function Home() {
  const [count, setCount] = useState(0)
  const ref = useRef()
  const init = useRef(true)

  useEffect(() => {
    let mounted = true
    if (ref.current) {
      const el = ref.current
      const greeting = el.querySelectorAll('.greeting')
      const tl = gsap
        .timeline({
          paused: true,
          repeat: 1,
          repeatDelay: 0.6,
          yoyo: true,
          onComplete: () => {
            if (mounted) {
              setCount(c => {
                const next = c + 1
                return next === data.length ? 0 : next
              })
            }

            if (init.current) {
              init.current = false
            }
            tl.restart()
          },
        })
        .fromTo(
          greeting,
          {
            autoAlpha: 0,
            yPercent: 100,
          },
          {
            autoAlpha: 1,
            yPercent: 0,
            duration: 1,
            stagger: 0.1,
          },
        )
      tl.play()
    }

    return () => {
      mounted = false
    }
  }, [])

  return (
    <div
      className="home-wrap"
      ref={ref}
      style={{backgroundColor: data[count].color}}
    >
      <Navigation />
      <div className="home-content">
        <ul className="home-text-animation">
          {[1, 2, 3, 4, 5].map(val => (
            <li key={val}>
              <div className="greeting">{data[count].text}</div>
            </li>
          ))}
        </ul>

        <div className="feature-imgs-wrap">
          <div className="feature-img-mask">
            {data.map((item, i) => {
              const previous = count - 1 < 0 ? data.length - 1 : count - 1
              return (
                <div
                  key={item.src}
                  className={clsx({
                    'feature-img': true,
                    enter: count === i,
                    exit: init.current ? false : previous === i,
                  })}
                  style={{backgroundImage: `url(${item.src})`}}
                />
              )
            })}
          </div>
        </div>
      </div>
    </div>
  )
}
