import React, {useCallback} from 'react'
import {preloader} from '../utils/preloader'
import imagesLoaded from 'imagesloaded'
import {gsap} from 'gsap'

function Sandbox() {
  const [loading, setLoading] = React.useState(true)
  const srcs = [1, 2, 3, 4, 5].map(
    num => `https://source.unsplash.com/random/800x600?${num}`,
  )

  const ref = React.useRef()

  //   const ref = useCallback(node => {
  //     if (node !== null) {
  //       imagesLoaded(node.querySelectorAll('img'), () => {
  //         gsap.set(node, {display: 'block'})
  //         gsap.timeline().from(node.querySelectorAll('img'), {
  //           autoAlpha: 0,
  //           duration: 0.4,
  //           stagger: 0.1,
  //         })
  //       })
  //     }
  //   }, [])

  React.useEffect(() => {
    preloader(srcs).then(() => {
      setLoading(false)
    })
  }, [srcs])

  if (loading) {
    return <div>Loading...</div>
  }

  return (
    <div ref={ref}>
      {srcs.map(src => (
        <img key={src} src={src} alt="dummy" />
      ))}
    </div>
  )
}

export {Sandbox}
