import React from 'react'
import {Link} from 'react-router-dom'
import {MenuButton} from '../menu-button/index'
import './navigation.css'

function Navigation() {
  const [hover, setHover] = React.useState(false)
  // const { enter, leave } = useCursor()
  return (
    <>
      <nav className="nav">
        <Link
          to="/works"
          className="nav-link"
          onMouseEnter={() => {
            setHover(true)
          }}
          onMouseLeave={() => {
            setHover(false)
          }}
        >
          <MenuButton hover={hover.toString()} /> Works
        </Link>
        <Link to="/about" className="nav-link">
          About
        </Link>

        {/* <Link to='/ar' className='nav-link' onMouseEnter={enter} onMouseLeave={leave}>
					Archive
				</Link> */}
      </nav>
    </>
  )
}

export {Navigation}
