import React from 'react'
import {useHistory} from 'react-router-dom'
import styled, {keyframes} from 'styled-components'

const Button = styled.button`
  display: flex;
  color: ${props => (props.variant === 'light' ? '#fafafa' : '#111')};
  text-decoration: none;
  font-size: 16px;
  font-weight: 400;
  position: fixed;
  top: 3vh;
  left: 1vw;
  overflow: hidden;
  background: none;
  border: none;
  z-index: 10;
  cursor: pointer;
`

const fadeIn = keyframes`
    from {
        opacity: 0;
        transform: translate3d(0, 100%, 0);
    }
    to {
        opacity: 1;
        transform: translate3d(0, 0, 0);
    }
`

const FadeUp = styled.div`
  display: flex;
  align-items: center;
  animation: ${fadeIn} 0.6s backwards var(--base-ease);
  animation-delay: ${props => props.delay + 's'};
`

const Label = styled.span`
  display: inline-block;
  margin-left: 8px;
`

export function BackButton({delay = 0, variant = 'light', to = '/'}) {
  const history = useHistory()
  function handleClick() {
    history.push(to)
  }

  return (
    <Button onClick={handleClick} variant={variant}>
      <FadeUp delay={delay}>
        <svg
          fill="none"
          width="20"
          height="20"
          viewBox="0 0 24 24"
          stroke="currentColor"
        >
          <path
            strokeLinecap="round"
            strokeLinejoin="round"
            strokeWidth="2"
            d="M10 19l-7-7m0 0l7-7m-7 7h18"
          />
        </svg>
        <Label>back</Label>
      </FadeUp>
    </Button>
  )
}
