import React from 'react'
import './loading.css'

export function Loading({text = 'LOADING'}) {
  const reverseChars = text.toUpperCase().split('').reverse()

  return (
    <div className="loading">
      {reverseChars.map((char, i) => (
        <div key={i}>{char}</div>
      ))}
    </div>
  )
}
